import React, { Component } from 'react';
import {connect} from 'react-redux';
import { Table,Modal, Form, Input,Button ,message,Select,Icon,Upload,InputNumber} from 'antd';
import {bannerList,savebanner,getBannerInfo,deleteBanner} from '../../assets/js/api';
import {loadingChangeFn, qiniuChangeFn} from '../../store/action';
import './banner.css';
const FormItem = Form.Item;
const confirm = Modal.confirm;
const Option = Select.Option;

let Banner = class Banner extends Component {
    constructor(props, context) {
        super(props, context);
        this.state={
            modalVisible: false,
            modalTitle:'banner',
            id:'',
            image: '',
            imageUrl:'',
            data:[],
            columns:[{
                title: 'banner',
                dataIndex: 'title',
                key: '0'
            }, {
                title: 'banner',
                key: '1',
                render: (record) => (<img className="bannerCover"
                                          src={record.imageUrl}
                />)
            },{
                title: '',
                dataIndex: 'desc',
                width:'300px',
                key: '2'
            },{
                title: '',
                dataIndex: 'sort',
                key: '3'
            }, {
                title: 'banner',
                key: '4',
                render: (record) => <div>{record.isShow === 'YES'?<span></span>:<span></span>}</div>
            }, {
                title: '',
                key: '5',
                render: (record) => <div><a onClick={this.editMenu.bind(this,record)}></a> <a onClick={this.deleteMenu.bind(this,record)}></a></div>
            }],
            loading:false,
            conditionIsShow:'',
            isShow:'YES',
            title:'',
            total:0,
            current:1,
            defaultCurrent:1,
            pageSize:10,
            qiniuHost:'',
            qiniuToken:''
        }
    }
    //
    componentDidMount(){
        let fuRuiAccount = sessionStorage.getItem('fuRuiAccount');
        if (fuRuiAccount) {
            fuRuiAccount = JSON.parse(fuRuiAccount);
            if (fuRuiAccount.account && fuRuiAccount.password) {
                this.setState({
                    qiniuHost:fuRuiAccount.qiniuHost,
                    qiniuToken:fuRuiAccount.qiniuToken
                })
            }
        }
        this.getMemberList(1);
    }
    //banner
    getMemberList(current){
        let _this = this;
        _this.setState({
            loading:true
        });
        let data = {
            isShow:this.state.conditionIsShow,
            title:this.state.title,
            current:current,
            size:this.state.pageSize
        };
        bannerList(data).then(res => {
            if(res.code === 0){
                for (let i in res.data.list){
                    res.data.list[i].key = i;
                }
                _this.setState({
                    loading:false,
                    data:res.data.list,
                    current:res.data.pageNo,
                    pageSize:res.data.pageSize,
                    total:res.data.totalCount
                });
            }
        });
    }
    //
    setModalVisible() {
        this.setState({
            modalVisible:false,
            id:''
        });
    }
    //banner
    addMenu(){
        this.props.form.resetFields();
        this.setState({
            modalVisible:true,
            id:'',
            isShow:'YES',
            image:'',
            imageUrl:'',
            modalTitle:'banner'
        });
        this.props.form.setFieldsValue({
            title:'',
            desc:'',
            sort:1,
        });
    }
    //banner
    editMenu(record){
        let _this = this;
        _this.setState({
            modalVisible:true,
            id:record.id,
            image: record.image,
            imageUrl:record.imageUrl,
            isShow:record.isShow,
            modalTitle:'banner'
        });
        _this.props.form.setFieldsValue({
            title:record.title,
            desc:record.desc,
            sort:record.sort,
        });
    }
    //banner
    deleteMenu(record){
        let _this = this;
        confirm({
            title: ''+record.title+'?',
            cancelText:'',
            okText:'',
            onOk() {
                deleteBanner({data:{id:record.id}}).then(res => {
                    if(res.code === 0){
                        message.success('');
                        _this.getMemberList(1);
                    }
                });
            }
        });
    }
    //banner
    confirmFn(){
        let _this = this;
        this.props.form.validateFields((errors, values) => {
            if (!!errors) {
                return;
            }
            let data = {
                id:_this.state.id,
                title:values.title,
                desc:values.desc,
                sort:values.sort,
                image:_this.state.image,
                isShow:_this.state.isShow,
            };
            _this.props.dispatch(loadingChangeFn(true));
            savebanner(data).then(res => {
                _this.props.dispatch(loadingChangeFn(false));
                if(res.code === 0){
                    message.success('');
                    _this.setState({
                        modalVisible:false
                    });
                    _this.getMemberList(1);
                }
            });
        });
    }
    selectChange(value){
        this.setState({conditionIsShow:value});
    }
    editChange(value){
        this.setState({isShow:value});
    }
    render(){
        let _this = this;
        const { getFieldProps } = this.props.form;
        const props = {
            name: 'file',
            action: 'http://up.qiniu.com',
            showUploadList:false,
            accept:'image/gif,image/jpeg,image/png,image/jpg',
            listType:'picture',
            data:{
                token:_this.state.qiniuToken  //token
            },
            beforeUpload(info){
                let type = info.name.replace(/.+\./, "");//
                if(type.toLowerCase() == "gif" || type.toLowerCase() == "jpg" || type.toLowerCase() == "jpeg" || type.toLowerCase() == "png"){

                }else {
                    message.error(",");
                    return false;
                }
            },
            onChange(info) {
                if (info.file.status !== 'uploading') {
                    if(info.fileList[info.fileList.length - 1].response){
                        _this.setState({
                            image: info.fileList[info.fileList.length - 1].response.key,
                            imageUrl:_this.state.qiniuHost + info.fileList[info.fileList.length - 1].response.key
                        });
                    }
                }
                if (info.file.status === 'done') {
                    message.success(`${info.file.name} `);
                } else if (info.file.status === 'error') {
                    _this.props.dispatch(qiniuChangeFn(true));
                    message.error(`${info.file.name} !`);
                }
            }
        };
        return (
            <div className="bannerBox">
                <div className="contentBox">
                    <div className="titleBox">
                        <Select defaultValue={this.state.conditionIsShow}
                                onChange={this.selectChange.bind(this)}
                                style={{ width: 200 }}
                                value={this.state.conditionIsShow}
                        >
                            <Option value=""></Option>
                            <Option value="YES"></Option>
                            <Option value="NO"></Option>
                        </Select>
                        <Input placeholder="banner" defaultValue={this.state.title} onChange={(e)=>{this.state.title = e.target.value}} className="searchInput"/>
                        <Button type="primary" onClick={this.getMemberList.bind(this,1)}></Button>
                        <Button onClick={this.addMenu.bind(this)} className="add"
                                type="primary"
                        >banner</Button></div>
                    <Table columns={this.state.columns}
                           dataSource={this.state.data}
                           loading={this.state.loading}
                           pagination={{
                               showQuickJumper:true,
                               defaultCurrent:this.state.defaultCurrent,
                               total:this.state.total,
                               current:this.state.current,
                               pageSize:this.state.pageSize,
                               onChange(num){
                                   _this.getMemberList(num);
                               }
                           }}
                    />
                </div>
                <Modal
                    cancelText=""
                    okText=""
                    onCancel={() => this.setModalVisible()}
                    onOk={() => this.confirmFn(false)}
                    title={this.state.modalTitle}
                    visible={this.state.modalVisible}
                    wrapClassName="vertical-center-modal"
                >
                    <Form className="bannerForm">
                        <FormItem label="banner">
                            <Input
                                placeholder="banner"
                                {...getFieldProps('title',{
                                    rules: [
                                        {required: true, min:1, message: '1'},
                                        {required: true, max:40, message: '40'}
                                    ]
                                })}
                            />
                        </FormItem>
                        <div className="newsTitle">banner:</div>
                        <Upload {...props}>
                            <div className="uploadBox">
                                {this.state.imageUrl?<img src={this.state.imageUrl}/>:''}
                                {this.state.imageUrl?'':<Icon type={'plus'} />}
                            </div>
                        </Upload>
                        <FormItem label="banner">
                        <Input
                            placeholder="banner"
                            {...getFieldProps('desc',{
                                rules: [
                                    {required: true, min:1, message: '1'},
                                    {required: true, max:100, message: '100'}
                                ]
                            })}
                        />
                        </FormItem>
                        <FormItem label="banner">
                            <InputNumber
                                className="inputNum"
                                min={1}
                                max={100}
                                placeholder=""
                                {...getFieldProps('sort',{
                                    rules: [
                                        {required: true ,message: ''}
                                    ]
                                })}
                            />
                        </FormItem>
                        <div className="newsTitle">banner:</div>
                        <Select defaultValue={this.state.isShow}
                                onChange={this.editChange.bind(this)}
                                style={{ width: 200 }}
                                value={this.state.isShow}
                        >
                            <Option value="YES"></Option>
                            <Option value="NO"></Option>
                        </Select>
                    </Form>
                </Modal>
            </div>
        )
    }
};
Banner = Form.create()(Banner);
export default connect()(Banner);