import React, { Component } from 'react';
import {connect} from 'react-redux';
import { Table,Modal, Form,Button,message} from 'antd';
import './menu.css';
import {getNavigateInfo,deleteNavigate} from '../../assets/js/api';
const confirm = Modal.confirm;

let Menu = class Menu extends Component {
    constructor(props, context) {
        super(props, context);
        this.state={
            modalVisible: false,
            modalTitle:'',
            id:'',
            parentId:'',
            parentName:'',
            data:[],
            columns:[{
                title: '',
                dataIndex: 'navigateName',
                key: '0'
            }, {
                title: '',
                dataIndex: 'url',
                key: '1'
            }, {
                title: '',
                key: '2',
                render: (record) => <div>{record.navigateType === 'URL'?<span></span>:<span></span>}</div>
            }, {
                title: '',
                dataIndex: 'sort',
                key: '3'
            }, {
                title: '',
                key: '4',
                render: (record) => <div><a onClick={this.editMenu.bind(this,record.id)}></a> <a onClick={this.deleteMenuFn.bind(this,record)}></a></div>
            }],
            paginationShow:false,
            loading:false
        }
    }
    //
    componentDidMount(){
        let menuId = sessionStorage.getItem("frzMenuId");
        this.getMenuList(menuId);
    }
    //
    getMenuList(id){
        let _this = this;
        _this.setState({
            loading:true
        });
        getNavigateInfo({id:id}).then(res => {
            if(res.code === 0){
                for (let i in res.data.subNavigates){
                    res.data.subNavigates[i].key = i;
                }
                _this.setState({
                    loading:false,
                    parentId:res.data.id,
                    parentName:res.data.navigateName,
                    data:res.data.subNavigates
                });
            }
        });
    }
    //
    addMenu(){
        sessionStorage.setItem("frzMenuEditId",'');
        this.props.history.push('/MenuEdit');
    }
    //
    editMenu(id){
        sessionStorage.setItem("frzMenuEditId",id);
        this.props.history.push('/MenuEdit');
    }
    //
    deleteMenuFn(record){
        let _this = this;
        let parentId = _this.state.parentId;
        confirm({
            title: ''+record.navigateName+'?',
            cancelText:'',
            okText:'',
            onOk() {
                deleteNavigate({data:{id:record.id}}).then(res => {
                    if(res.code === 0){
                        message.success('');
                        _this.getMenuList(parentId);
                    }
                });
            }
        });
    }
    goBackFn(){
        window.history.back();
    }
    render(){
        return (
            <div className="menuBox">
                <div className="contentBox">
                    <div className="titleBox">
                        <span className="leftTitle"></span><span className="nextTitle">{this.state.parentName}</span>
                        <Button  onClick={this.addMenu.bind(this)}
                            type="primary"
                        ></Button>
                        <Button
                            onClick={this.goBackFn.bind(this)}
                            type="primary"
                        ></Button>
                    </div>
                    <Table columns={this.state.columns}
                        dataSource={this.state.data}
                        loading={this.state.loading}
                        pagination={this.state.paginationShow}
                    />
                </div>
            </div>
        )
    }
};
Menu = Form.create()(Menu);
export default connect()(Menu);